<template>
    <div>
       <div style="position:fixed;top:10px;left:100px;z-index:100000;width:50px;">
        <button @click="listsearch=!listsearch" type="button"  class="btn btn-sm btn-dark  "><span class="typcn typcn-zoom"></span></button>
      </div>
      <div v-show="listsearch" style="position:fixed;left:100px;top:50px;z-index:1000;">
        <form action="" @submit.prevent="cek" >
          <div class="sm-form ">
              <input type="text" id="search" name="search" style="width:50vw;" class="form-control form-control-sm" placeholder="search" v-model="search" >
          </div>
        </form>
          <div v-for="(item, index) in searchs" :key="index+'searchs'" @click="cari(item)" class="bg-white ">
            <p class="p-2 hover:bg-blue-300 cursor-pointer" >
              {{item.label}}
            </p>
        </div>
      </div>
      
      <div style="position:fixed;top:10px;left:60px;z-index:100000;width:50px;">
        <button @click="listkategori=!listkategori" type="button"  class="btn btn-sm btn-dark  "><span class="typcn typcn-th-large"></span></button>
      </div>
       <div v-show="listkategori" style="position:fixed;top:50px;left:60px;z-index:100000;width:30vw;" class="shadow rounded-lg p-3 bg-white animated fadeInLeft">
           <!-- ============================= -->
            <div  class="bord   text-left pl-3 shadow rounded-lg" style="font-size:13px;"
            v-for="(item, index) in listPeta" :key="index+'listpeta'"
              @click="bawah($event);"
            >
              <span class="typcn typcn-map" @click="parent($event)">
                <span  class="pl-2">{{item.kategori}}</span>
              </span>
              <span class="typcn typcn-arrow-down float-right"></span>
              <ul class="isaktif  anim mb-3  animated slideInLeft " >
                <li class="bord  text-left p-1 shadow" style="font-size:13px;" >
                  <p  @click="muncul($event);" class="p-1">
                    <!-- note : checkedNames harus dalam bentuk array -->
                    <input type="checkbox" id="marker" value="marker" class=""   @click="loadmarker(item.id)">
                    <label for="marker">
                    </label>
                    <span class="typcn typcn-pin"></span>Custom Marker
                  </p>
                  <div class="isaktif">
                    <div v-for="(item2, index2) in listMarker" :key="index2+'icon'" v-show="item2.id_peta==item.id" class="hover:bg-green-400 text-xs pl-3"
                    >
                      <span class="typcn typcn-arrow-right ml-1"></span> {{item2.title}}
                    </div>
                  </div>
                </li>
                <li class="bord  text-left p-1 shadow" style="font-size:13px;" >
                  <p  @click="muncul($event);">
                    <span class="typcn typcn-map"></span> Geojson
                  </p>
                  <div class="isaktif">
                    <div
                     v-for="(item3, index3) in listGeojson" :key="index3+'icon'" v-show="item3.id_peta==item.id" class="hover:bg-green-400"
                     >
                      <input type="checkbox" @click="loadgeojson(item3)" v-model="layers['geojson'+item3.id+'on']" class="ml-3">
                      <span class="typcn typcn-arrow-right ml-1"></span> {{item3.title}}
                    </div>
                  </div>
                </li>
              </ul>
            </div>
        </div>
       <div style="position:fixed;top:10px;right:40px;z-index:100000;min-width:50px;" class="">
         <button @click="tilem=!tilem" type="button" class="btn btn-sm btn-dark  "><span class="typcn typcn-map"></span></button>
       </div>
       <div v-show="tilem" style="position:fixed;top:50px;right:10px;z-index:100000;min-width:150px;" class="shadow rounded-lg p-3 bg-white animated fadeIn">
         <p class="p-1 textxs">Layer Tile : </p>
         <div v-for="(item, index) in tilemap" :key="index+'tilemap'" class="text-sm">
             <input type="radio" :id="item" :value="item" v-model="pilihtilemap">
             <label class="ml-2" :for="item"> {{item.tile}}</label>
             <br>
         </div>
        </div>
        <div v-if="ready">
         <div id="mapid" style="height:100vh;">
            
         </div>
        </div>
    </div>
</template>
<script>
import autophp from "@/plugins/autophp.js"; // ganti source nya
import _ from 'lodash'
import axios from 'axios'
let sdb = new autophp();
let maps;
export default{
    data(){
        return{
          ready:false,
          listsearch:false,
          search:'',
          searchs:[],
          center:[-2.3662494, 115.4641079],
          loading:true,
          listPeta:[],
          listMarker:[],
          listGeojson:[],
          listgeojson:[],
          geojson:[],
          geoJsonLayer:{},
          layers:{},
          markers:{},
          listkategori:false,
          tilem:false,
          tilemap:[
            {
              type:'openmap',
              tile:'open streetmap',
              link:'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'
            },
            // GOOGLE
            {
              type:'google',
              tile:'street',
              link:'http://{s}.google.com/vt/lyrs=m&x={x}&y={y}&z={z}'
            },
            {
              type:'google',
              tile:'hybrid',
              link:'http://{s}.google.com/vt/lyrs=s,h&x={x}&y={y}&z={z}'
            },
            {
              type:'google',
              tile:'satelite',
              link:'http://{s}.google.com/vt/lyrs=s&x={x}&y={y}&z={z}'
            },

          ],
          pilihtilemap: {
              type:'openmap',
              tile:'open streetmap',
              link:'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'
            },
        }
    },
    watch:{
      pilihtilemap(){
        this.ready=false;
        maps=null;
        setTimeout(() => {
          this.loadmap()
         
        }, 1000);
      },
      search(){
        // this.cek()
      }
    },
      methods: {
        async cek(){
          let that=this
          const provider = new GeoSearch.OpenStreetMapProvider();
          const results = await provider.search({ query: that.search});
          this.searchs=results
        },
        cari(item){
          console.log(item)
          maps.panTo(new L.LatLng(item.y,item.x))
          this.searchs=[]
        },
        bawah(e) {
          if(e.target.querySelectorAll('ul').length>0){
            e.target.querySelectorAll('ul')[0].classList.toggle('isaktif')
          }
        },
        muncul (e) {
          e.target.nextElementSibling.classList.toggle('isaktif')
        },
         parent(e){
          e.target.parentNode.click();
        },
        loadListPeta(){
          let that=this;
          sdb.collection("app_gis_kategori").doc().get("*","order by id").then(res => {
            that.listPeta=res;
            that.$forceUpdate();
          });
          sdb.collection("app_gis_marker").doc().get("*","order by id").then(res => {
            that.listMarker=res;
            that.$forceUpdate();
          });
          sdb.collection("app_gis_geojson").doc().get("*","order by id").then(res => {
            that.listGeojson=res;
            // let leftjoin = _.map(that.listMarker, function(obj) {
            //     return _.assign(
            //         obj,
            //         _.find(that.listPeta, {
            //         id: obj.id_peta
            //         })
            //     );
            // });
            // console.log('leftjoin',leftjoin)
            that.$forceUpdate();
          });
        },
        loadmarker(item){
          // console.log(item)
          let that=this;
          let data = this.listMarker
          data=data.filter((e)=>e.id_peta==item)
           if(data.length>0){
             data.forEach((e,i)=>{
              if ( maps.hasLayer(that.markers['marker'+e.id]) ){  // if the specified layer is visible on the map, it needs to be removed
                maps.removeLayer(that.markers['marker'+e.id])
                that.markers['marker'+e.id+'on']=false
                return
              }
              that.markers['marker'+e.id+'on']=true
              console.log('listicon',e)
              if(i==0){
                maps.panTo(new L.LatLng(e.lat, e.lng))
              }
            // =================================================================
                if(e.kategori=='icon'){
                  console.log('icon',e)
                    var greenIcon = L.icon({
                        iconUrl: e.icon,
                        iconSize:     [28, 75], // size of the icon
                        iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                        popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                    });
                    if(e.popup=='click'){
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `, {maxWidth: 500}).addTo(maps)
                    }else{
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `,{ permanent: false, sticky: true }).addTo(maps)
                    }
            // =================================================================
                }else if(e.kategori=='gambar'){
                  console.log('gambar',e)
                     var greenIcon = L.icon({
                        iconUrl: e.gambar,
                        iconSize:     [48, 45], // size of the icon
                        iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                        popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                    });
                    if(e.popup=='click'){
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `, {maxWidth: 500}).addTo(maps)
                    }else{
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `,{ permanent: false, sticky: true }).addTo(maps)
                    }
            // =================================================================
                }else if(e.kategori=='radius'){
                  console.log('radius',e)
                    if(e.popup=='click'){
                          that.markers['marker'+e.id]=L.circle([e.lat, e.lng], {
                              color: "black",
                              fillColor: e.color,
                              fillOpacity: e.opacity,
                              radius: e.radius
                          }).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                        </div>
                        `, {maxWidth: 500}).addTo(maps);
                    }else{
                        that.markers['marker'+e.id]=L.circle([e.lat, e.lng], {
                              color: "black",
                              fillColor: e.color,
                              fillOpacity: e.opacity,
                              radius: e.radius
                          }).bindTooltip(`
                      <div class="text-xs">
                          <div class="font-bold uppercase">${e.title}</div>
                          <div class="text-justify">${e.deskripsi}</div>
                      </div>
                      `,{ permanent: false, sticky: true }).addTo(maps);
                    }
                }
        // =================================================================
            })
            }
        },
        loadgeojson(item){
          axios.get(item.geojson).then(res=>{
              if ( maps.hasLayer(this.layers['geojson'+item.id]) ){  // if the specified layer is visible on the map, it needs to be removed
                maps.removeLayer(this.layers['geojson'+item.id])
                this.layers['geojson'+item.id+'on']=false
                return
              }
              this.layers['geojson'+item.id]=L.geoJSON(res.data).addTo(maps);
              this.layers['geojson'+item.id+'on']=true
              if(res.data.features[0].geometry.type=='Polygon'){
                maps.panTo(new L.LatLng(res.data.features[0].geometry.coordinates[0][0][1],res.data.features[0].geometry.coordinates[0][0][0]))
              }else{
                maps.panTo(new L.LatLng(res.data.features[0].geometry.coordinates[0][1],res.data.features[0].geometry.coordinates[0][0]))
              }
              this.loadGeojsonLayer(this.layers['geojson'+item.id])
              this.loading = false;
              this.$forceUpdate();
          })
        },
        loadmap(){
            this.ready=true;
            maps=null;
            setTimeout(() => {
              maps = new L.Map('mapid');
              maps.setView(this.center, 13);
              // L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoicnVkeXNrYSIsImEiOiJja3B1cGY0OGwxOTlxMm5xdnJ6dTU3dXZiIn0.vqqfQlT3-d3KF9KNzemANg', {
              if(this.pilihtilemap.type=='openmap'){
                L.tileLayer(this.pilihtilemap.link, {
                  attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, Imagery Â© <a href="https://www.mapbox.com/">Mapbox</a>',
                  maxZoom: 18,
                  id: 'mapbox/streets-v11',
                  tileSize: 512,
                  zoomOffset: -1,
                  accessToken: 'pk.eyJ1IjoicnVkeXNrYSIsImEiOiJja3B1cGY0OGwxOTlxMm5xdnJ6dTU3dXZiIn0.vqqfQlT3-d3KF9KNzemANg'
                }).addTo(maps);
              }
              else if(this.pilihtilemap.type=='google'){
                L.tileLayer(this.pilihtilemap.link,{
                  maxZoom: 20,
                  subdomains:['mt0','mt1','mt2','mt3']
                }).addTo(maps);
              }
              // L.control.ruler().addTo(maps);
              // const search = new GeoSearch.SearchControl({
              //   provider: new GeoSearch.OpenStreetMapProvider(),
              // });
              // maps.addControl(search);
            }, 1000);
        },
        loadGeojsonLayer(layer){
              layer.eachLayer(function(layer) {
                setTimeout(() => {
                  // MENGGANTI ICON SECARA DYNAMIC SESUAI GEOSJON.IO
                  if(layer['_icon']){
                      let marker=``;
                      if(layer.feature.properties['marker-symbol']){
                          marker=marker+'-'+layer.feature.properties['marker-symbol'];
                      }
                      layer['_icon'].src=`https://a.tiles.mapbox.com/v4/marker/pin-m${marker}+${layer.feature.properties['marker-color'].replace('#','')}@2x.png?access_token=pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4NXFhYTA2bTMyeW44ZG0ybXBkMHkifQ.gUGbDOPUN1v1fTs5SeOR4A`;
                      layer['_icon'].style.height='80px'
                      layer['_icon'].style.width='30px'
                  }
                  // MENGGANTI POLYGON SECARA DYNAMIC
                  if(layer.feature.geometry.type=="Polygon"){
                      console.log('polygon')
                      // ====================
                      layer['_path'].setAttribute("fill", layer.feature.properties.fill);
                      layer['_path'].addEventListener('mouseover',function(){
                       layer['_path'].setAttribute("fill", '#000');
                      })
                      layer['_path'].addEventListener('mouseout',function(){
                       layer['_path'].setAttribute("fill", layer.feature.properties.fill);
                      })
                      // ====================
                      layer['_path'].setAttribute("stroke-opacity", layer.feature.properties['stroke-opacity']);
                      layer['_path'].setAttribute("stroke-width", layer.feature.properties['stroke-width']);
                      layer['_path'].setAttribute("stroke", layer.feature.properties.stroke);
                      layer['_path'].setAttribute("fill-opacity", layer.feature.properties['fill-opacity']);
                      // console.log(layer['_path'].setAttribute("fill", "#fff"))
                  }
                  // MENGGANTI POLYGON SECARA DYNAMIC
                  if(layer.feature.geometry.type=="LineString"){
                      console.log('LineString')
                      layer['_path'].setAttribute("stroke-opacity", layer.feature.properties['stroke-opacity']);
                      layer['_path'].setAttribute("stroke-width", layer.feature.properties['stroke-width']);
                      layer['_path'].setAttribute("stroke", layer.feature.properties.stroke);
                  }
              }, 1000);
              // TOOLTIP TEXT SAAT HOVER 
                let kecuali=['stroke','stroke-width','stroke-opacity','fill','fill-opacity','marker-color','marker-size','marker-symbol']
                let txt=`<table class="table table-sm table-striped">`;
                Object.keys(layer.feature.properties).filter(e=>{
                  if(!kecuali.includes(e)){
                    return e;
                  }
                }).forEach(e=>{
                  txt=txt+`
                  ${layer.feature.properties[e].indexOf('http')!=-1?`<div><a href="${layer.feature.properties[e]}" target="__blank"><img class="viewer" style="height:100px;width:100px;" src="${layer.feature.properties[e]}"></a></div>`:`
                    <tr><td class="capitalize">${e}</td><td>${layer.feature.properties[e]}</td></tr>                  
                  `}
                  `;
                })
                // layer.bindPopup(
                layer.bindTooltip(
                  txt+`</table>`,
                  { permanent: false, sticky: true }
                );
              });
        }
    },
    mounted() {
       this.loadListPeta();
        setTimeout(() => {
            this.loadmap()
          //    const provider = new GeoSearch.OpenStreetMapProvider();
          // console.log('provider',provider)
        }, 2000);
    },
     metaInfo: {
        title: 'Add Marker dynamic',
        meta: [
         { charset: 'utf-8' },
         { name: 'viewport', content: 'width=device-width, initial-scale=1' }
        ],
        link: [
         { rel: 'stylesheet', href: 'https://unpkg.com/leaflet@1.7.1/dist/leaflet.css', integrity:'sha512-xodZBNTC5n17Xt2atTPuE1HxjVMSvLVW9ocqUKLsCC5CXdbqCmblAshOMAS6/keqq/sMZMZ19scR4PsZChSR7A==',crossorigin:'' },
         { rel: 'stylesheet', href: 'https://cdn.jsdelivr.net/gh/gokertanrisever/leaflet-ruler@master/src/leaflet-ruler.css', integrity:'sha384-P9DABSdtEY/XDbEInD3q+PlL+BjqPCXGcF8EkhtKSfSTr/dS5PBKa9+/PMkW2xsY',crossorigin:'anonymous' },
         { rel: 'stylesheet', href: 'https://unpkg.com/leaflet-geosearch@3.0.0/dist/geosearch.css' },
        ],
        script: [
            { src: 'https://unpkg.com/leaflet@1.7.1/dist/leaflet.js', integrity: "sha512-XQoYMqMTK8LvdxXYG3nZ448hOEQiglfqkJs1NOQV44cWnUrBc8PkAOcXy20w0vlaXaVUearIOBhiXZ5V3ynxwA==", crossorigin: ''},
            { src: 'https://cdn.jsdelivr.net/gh/gokertanrisever/leaflet-ruler@master/src/leaflet-ruler.js', integrity: "sha384-N2S8y7hRzXUPiepaSiUvBH1ZZ7Tc/ZfchhbPdvOE5v3aBBCIepq9l+dBJPFdo1ZJ", crossorigin: 'anonymous'},
            { src: 'https://unpkg.com/leaflet-geosearch@3.0.0/dist/geosearch.umd.js'},
        ],
    },
}
</script>
<style scoped>
